<template>
	<div id="wheel-record">
		<public-header title="中奖纪录"></public-header>
		<div class="content">
			<van-tabs v-model="active" swipeable @click='onChangeStatus'>
				<van-tab v-for="(index,key) in 3" :title="title[index]" :key="key">
					<van-list v-model="record.loading" :finished="record.loaded" v-if="record.list.length > 0" @load="onLoad" :offset="100" :immediate-check="false">
						<div class="record-item" v-for="(item, index) in record.list" :key="index">
							<div class="record-body">
								<div class="record-inner">
									<span class="record-text-left">抽奖时间：</span>
									<span class="record-text-right">{{item.addtime}}</span>
								</div>
								<div class="record-inner">
									<span class="record-text-left">中奖情况：</span>
									<span class="record-text-right" v-if="item.type">{{item.type.text ? item.type.text : '无'}}</span>
									<span class="record-text-right" v-else>未中奖</span>
									
								</div>
								<div class="record-inner">
									<span class="record-text-left">奖品类型：</span>
									<span class="record-text-right" v-if="item.award_type">{{item.award_type.text ? item.award_type.text : '无'}}</span>
									<span class="record-text-right" v-else>无</span>
								</div>
								<div class="record-inner">
									<span class="record-text-left">奖品详情：</span>
									<span class="record-text-right" v-if="item.award_type">
										<template v-if="item.award_type.name != 'redpacket'">
											{{item.award_value}}{{item.award_type.name == 'credit1' ? '积分' : '元'}}
										</template>
										<template v-else v-for="da in item.award_value">
											{{da}}
										</template>
									</span>
									<span class="record-text-right" v-else>
										未中奖
									</span>
								</div>
							</div>
							<div class="record-footer" v-if="item.status == 0">
								<div @click="onGetPrize(item.id)" class="record-status">已领取</div>
							</div>

						</div>
					</van-list>
					<div class="no-data" v-else>
						<div class="bg"></div>
						<p>暂时没有中奖记录哦～</p>
					</div>
				</van-tab>
			</van-tabs>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import { Dialog } from 'vant';
	export default {
		components: {
			PublicHeader
		},
		data() {
			return {
				active: 0,
				now_active: 0,
				refresh: 0,
				status: -1,
				record: {
					page: 1,
					psize: 5,
					loading: false,
					loaded: false,
					list: [],
				},
				title: {
					1:'全部',
					2:'未领取',
					3:'已领取',
				},
			}
		},
		methods: {
			onChangeStatus(index, title) {
				if(this.now_active != index) {
					this.refresh = 1;
				}
				if(index == 0) {
					this.status = -1;
				} else if(index == 1) {
					this.status = 0;
				} else if(index == 2) {
					this.status = 1;
				} 
				this.onLoad();
			},
			onLoad() {
				if(this.refresh == 1) {
					this.record = {
						page: 1,
						psize: 5,
						loaded: false,
						loading: false,
						list:[]
					};
				}
				if(this.record.loaded) {
					return false;
				}
				this.record.loading = true;
				this.util.request({
					url: 'wheel/activity/record',
					data:{
						status: this.status,
						page: this.record.page,
						psize: this.record.psize,
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return;
					}
					result = result.message;
					this.record.list = [...this.record.list, ...result.records];
					if(this.record.list.length == 0) {
						this.record.loaded = true;
					}

					this.record.loading = false;
					if(result.records.length < this.record.psize) {
						this.record.loaded = true;
					}
					this.record.page++;
					this.now_active = this.active;
					this.refresh = 0;
				})	
			},
			onGetPrize(id) {
				/*var params = {
					url: 'wheel/activity/status?id=' + id,
					confirm: '确定领取赠品吗?',
				}
				this.util.jspost(params);*/
				Dialog.confirm({
					title: '温馨提示',
					message: '确定已领取赠品吗？'
				}).then(() => {
					this.util.request({
						url: 'wheel/activity/status',
						data:{
							id: this.id,
						}
					}).then((res) => {
						let result = res.data.message;
						this.$toast(result.message);
						if(result.errno) {
							this.$toast(result.message);
							return false;
						}
						this.refresh = 1;
						this.onLoad();
					})	
				})

			}
		},
		created() {
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#wheel-record .content{
		position: fixed;
		top: 46px;
		right: 0;
		left: 0;
		bottom: 0;
		width: 100%;
		background: #f5f5f5; 
		overflow-x: hidden;
		overflow-y: scroll;   
	}
	#wheel-record .content::-webkit-scrollbar {
		display: none;
	}
	#wheel-record .van-nav-bar{
		background-color: #ff2d4b!important;
		color: #fff!important;
	}
	#wheel-record .van-hairline--bottom .van-icon-left{
		color: #fff!important;
	}
	#wheel-record .content .van-tabs .record-item{
		width: 100%;
		margin-top: 10px;
		background: #fff;
		padding-left: 25px;
	}
	#wheel-record .content .van-tabs .record-item .record-body{
		width: 100%;
		padding: 10px 0;
		color: #333;
		text-align: left;
	}
	#wheel-record .content .van-tabs .record-item .record-body .record-inner{
		font-size: 14px;
		margin: 5px 0;
		line-height: 26px;
	}
	#wheel-record .content .van-tabs .record-item .record-body .record-inner .record-text-left{
		display: inline-block;
		width: 20%;
		color: #a3a3a3;
		white-space: nowrap;
		overflow: hidden;
	}
	#wheel-record .content .van-tabs .record-item .record-body .record-inner .record-text-right{
		display: inline-block;
		width: 70%;
		white-space: nowrap;
		overflow: hidden;
	}
	#wheel-record .content .van-tabs .no-data{
		padding-top: 30%;
		text-align: center;
	}
	#wheel-record .content .van-tabs .no-data .bg{
		display: inline-block;
		width: 160px;
		height: 160px;
		background: url(../../../static/img/no_order.png) no-repeat 0 0;
		background-size: cover;
	}
	#wheel-record .content .van-tabs .no-data p{
		margin: 0;
		color: #999;
		font-size: 18px;
		line-height: 27px;
	}
	#wheel-record .content .van-tabs .record-item .record-footer{
		padding: 10px 0;
		margin-right: 10px;
		border-top: 1px solid #e0e0e0;
		overflow: hidden;
	}
	#wheel-record .content .van-tabs .record-item .record-footer .record-status{
		display: inline-block;
		float: right;
		margin-right: 10px;
		font-size: 14px;
		padding: 5px 0;
		width: 70px;
		border: 1px solid #ccc;
		text-align: center;
		border-radius: 3px;
		color: #333;
	}
</style>


